.prei {
  font-weight: 600;
  font-size: 22px;
  margin-bottom: 15px;
  padding-left: 10px;
}

.admin {
  padding: 20px;
  background-color: #f9f9f9;
}

.container {
  max-width: 800px;
  margin: auto;
}

.crud-section {
  margin-bottom: 20px;
  border: 1px solid #ccc;
  padding: 15px;
  border-radius: 8px;
  background-color: #fff;
}

h2 {
  margin-bottom: 10px;
  font-weight: 500;
}

.form-control-item {
  display: block;
  margin-bottom: 10px;
  padding: 8px;
  width: 100%;
  border: 1px solid #ccc;
  border-radius: 4px;
}

.delete-method-selector {
  margin-bottom: 10px;
}

.delete-method-selector label {
  margin-right: 15px;
  cursor: pointer;
}

.delete-method-selector input {
  margin-right: 5px;
}

.form-control-item:focus {
  display: block;
  margin-bottom: 10px;
  padding: 8px;
  width: 100%;
  border: 1px solid #0098c8;
  border-radius: 4px;
}

.CRUD-btn {
  background-color: #0098c8;
  color: #fff;
  border: none;
  padding: 10px 15px;
  border-radius: 5px;
  cursor: pointer;
  font-size: 16px;
}

.CRUD-btn:hover {
  background-color: #0184ac;
}

ul {
  list-style-type: none;
  padding: 0;
}

li {
}

li .id {
  padding-left: 10px;
}

img {
  margin-top: 5px;
  border-radius: 4px;
}

.product-table {
  width: 100%;
  border-collapse: collapse;
  margin-top: 20px;
}

.product-table th,
.product-table td {
  border: 1px solid #ddd;
  padding: 8px;
  text-align: left;
}

.product-table th {
  background-color: #f4f4f4;
  font-weight: bold;
}

.check {
  color: red;
  font-size: 14px;
  margin-top: 5px;
}

.form-control-item {
  margin-bottom: 10px;
  padding: 8px;
  font-size: 16px;
  width: 100%;
}
